$xs-breakpoint: 0; // Extra small screen / phone
$xs-lg-mobile-breakpoint: 425px; // large size phone
$xs-md-mobile-breakpoint: 375px; // medium size phone
$xs-sm-mobile-breakpoint: 320px; // small size phone
$sm-breakpoint: 576px; // Small screen / phone
$md-breakpoint: 768px; // Medium screen / tablet
$lg-breakpoint: 992px; // Large screen / desktop
$xl-breakpoint: 1200px; // Extra large screen / wide desktop

@mixin md-phone-up {
    @media (min-width: $xs-md-mobile-breakpoint) { @content; }
}

@mixin lg-phone-up {
    @media (min-width: $xs-lg-mobile-breakpoint) { @content; }
}

@mixin tablet-portrait-up {
    @media (min-width: $md-breakpoint) { @content; }
}

@mixin desktop-up {
    @media (min-width: $lg-breakpoint) { @content; }
}

@mixin wide-desktop-up {
    @media (min-width: $xl-breakpoint) { @content; }
}